body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
.outline {
  outline: 5px solid blue;
  outline: 5px solid red;
  border: 5px solid blue;
  border: 5px solid red;
}
#node:focus {
  border: 25px dashed #97fbb6;
  outline: none;
}
.path {
  stroke-width: 5;
  fill: #4a4c4c;
}
.treeWrapper {
  height: 70vh;
  position: fixed;
  left: 0;
  right: 0;
}
.manageTree {
  height: 30vh;
  display: flex;
  flex-direction: row;
  background-color: #ebebeb;
  padding: 20px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
.manageTree:focus {
  border: 7px dashed #97fbb6;
  outline: none;
}
.manageTree .sectionHeader {
  font-weight: 700;
  margin-bottom: 10px;
}
.dogDetails {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  /* border: 5px solid blue; */
}
.dogAttributes {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 50%;
}
.dogAttributes div {
  /* margin-bottom: 5px;
  margin-right: 5px; */
}
.controls {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  /* border: 5px solid blue; */
}
.about {
  display: flex;
  flex-grow: 0;
  flex-direction: column;
  /* border: 5px solid blue; */
}
